#taggbox_main *,
#taggbox_main *::before,
#taggbox_main *::after {
  box-sizing: border-box;
}

.taggbox{
  overflow: auto;
}

.tb_app_container *,
.tb_app_container *::before,
.tb_app_container *::after {
  box-sizing: border-box;
}

.tb_app_container{
  width: 100%;
}

.tb_app_container a{
  text-decoration: none !important;
  color: #2b2b2b;
}
.tb_spt_share_wrapper_dropdown .tb_spt_share_icon_list a svg path{
  fill: #ccc;
}
.tb_spt_share_wrapper_dropdown .tb_app_container a:hover svg path{
  fill: #fff;
}
.alert-enter {
  opacity: 0;
  transform: scale(0.9);
}
.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}
.tb_text_hashtag {
    display: inline;
}

.tb_app_container [tabindex="-1"]:focus, .tb_app_container [tabindex="-1"]:hover, .tb_app_container [tabindex="-1"]:active {
  outline: 0;
  box-shadow: none;
}

.onsite_submit_post_main{
  @import "./scss/uppyUploader.scss";
}

.tb_post_modal_modal_body{
  @import '@splidejs/react-splide/css/core';
}

